/* island */

.island {
    display: block;
    margin: @line-height-computed auto; //20px auto
    border-radius: @border-radius-large;
    background: @paper;
    position: relative;
}

@media (max-width: @screen-xs-max ) {

    /* 1 - compensate containers paddings */
    .island {
        min-width: 280px;
        margin: 0 -((@grid-gutter-width / 2)); /* 1 */
        border-radius: 0;
        border-top: 7px solid fade(@brand-alt-highlight, 10%);
        //background: #FFF;
        //color: @gray-darker;
    }

    /* 1 - avoid double compensation */
    .island .island {
        margin: 0; /* 1 */
        border-top: none;
    }

}

.island--title {
    padding-top: (@line-height-computed * 2); //40px
    overflow: hidden;
    line-height: 1.3;
    text-align: center;
    //color: @brand-primary-dark;
    color: @ink;

    &:before,
    &:after {
        content: '';
        position: relative;
        display: inline-block;
        width: 100%;
        height: 1px;
        vertical-align: middle;
        border-bottom: 2px solid (fade(@brand-alt-highlight, 20%));
    }

    &:before {
        left: -0.5em;
        margin: 0 0 0 -100%;
    }

    &:after {
        left: 0.5em;
        margin: 0 -100% 0 0;
    }

    > span {
        display: inline-block;
        vertical-align: middle;
    }

}

@media (max-width: @screen-xs-max ) {

    .island--title {
        border-bottom: 1px solid fade(@brand-alt-highlight, 10%);
        padding-bottom: (@line-height-computed / 2);

        &:before,
        &:after {
            content: none;
        }

        > span > br {
            display: none;
        }

    }

}

.island--title-blue {
    color: @brand-alt-dark;
    padding-bottom: @line-height-computed;

    &:before,
    &:after {
        content: none;
    }
}

.island--title-alt {
    color: @brand-alt-highlight;
    padding-bottom: @line-height-computed;

    &:before,
    &:after {
        content: none;
    }
}

