//    .island--title,
//    .island--title-blue,
//    .island--title-alt {
//        color: @ink;
//    }
//}

.island--title-size-large {
    font-size: (@font-size-h1 * 2);
    line-height: 1;

    &:before,
    &:after {
        content: none;
        display: none;
    }
}

@media (max-width: @screen-xs-max ) {

    .island--title-size-large {
        border-bottom: none;
    }

}

.island--separator {
    height: 1px;
    margin: @line-height-computed (-(@line-height-computed));
    padding: 0;
    background: transparent;
    border: none;
    border-bottom: 2px solid (fade(@brand-alt-highlight, 20%));

    .island-default & {
        margin-left:  (-((@line-height-computed)*3));
        margin-right: (-((@line-height-computed)*3));

        @media (max-width: @screen-xs-max) {
            margin-left:  (-(@line-height-computed));
            margin-right: (-(@line-height-computed));
        }
    }
}

.island--title-popover-trigger {
    position: relative;
    z-index: 10;

    .island--title-popover-icon {
        position: relative;
        z-index: 11;
        vertical-align: top;
        cursor: pointer;
    }
}

.island .island--title-popover {
    max-width: 50%;
    color: @gray-dark;
    font-size: @font-size-base;
    font-weight: normal;
    text-align: left;
    box-shadow: 0 5px 15px rgba(0, 0, 0, .5);

    .popover-content {
        padding: 25px;

        h2 {
            margin-top: 0;
            margin-bottom: 10px;
            font-size: @font-size-base;
        }

        hr {
            padding: 10px 0 0;
            margin: 0 -25px 20px;
            border-width: 1px;
        }

        p {
            padding: 10px 0;
            margin: 0;
            font-size: 13px;
            line-height: 18px;
            text-align: justify;

            &:first-child {
                padding-top: 0;
            }
        }

        .island--title-popover-close {
            display: block;
            cursor: pointer;
            text-align: center;

            .i--close {
                height: 10px;
                width: 10px;
                vertical-align: baseline;
            }

            &:hover {
                text-decoration: none;
            }
        }
    }

    @media (max-width: @screen-xs-max ) {
        max-width: 80%;
    }
}

.explanatory {
    font-size: @font-size-h2;
    line-height: 1;
    text-align: center;

    + .island--title {
        margin-top: 0;
        padding-top: 0;
    }
}

/* default theme */

.island-default {
    padding: 40px 60px;

    .island-sticky {
        margin-left: -60px;
        margin-right: -60px;
        padding-left: 60px;
        padding-right: 60px;

    }
}

.island-sticky .list-inline {
    padding-top: 15px;
    padding-bottom: 15px;

    li {
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: 15px;

        &:after {
            position: relative;
            top: -1px;
            content: '|';
            display: inline-block;
            margin: 0 0 0 15px;
            color: rgba(255, 255, 255, 0.25);
        }

        &:last-child:after {
            display: none;
        }

    }
}

.island-default .island--title {
    margin-left: -60px;
    margin-right: -60px;
    padding-left: 60px;
    padding-right: 60px;
}
.island-default .carousel-indicators-content {
    margin-left: -60px;
    margin-right: -60px;
}

.island-default > .island--title:first-child {
    margin-top: -40px;
    margin-bottom: 30px;
}

.island--title-no-mar-bottom,
.island-default > .island--title:first-child.island--title-no-mar-bottom {
    margin-bottom: 0;
}

.island--title-no-mar-top {
    margin-top: 0;
}

.island--title-half-mar-top {
    margin-top: (@line-height-computed - (@line-height-computed / 4)); //15px
}

.island--title-big-mar-top {
    margin-top: (@line-height-computed * 2); //45px
}

.island--title-no-pad {
    padding-top: 0;
    padding-bottom: 0;
}

.island--title-no-pad-top {
    padding-top: 0;
}

.island--title-half-pad-top {
    padding-top: @line-height-computed; //20px
}

.island--title-half-pad-bottom {
    padding-bottom: @line-height-computed; //20px
}

.island--title-big-pad-bottom {
    padding-bottom: (@line-height-computed * 2); //40px
}

@media (max-width: @screen-xs-max ) {
    .island-default {
        padding-left: 20px;
        padding-right: 20px;
    }

    .island-default .island--title {
        margin-left: -20px;
        margin-right: -20px;
        padding-left: 20px;
        padding-right: 20px;
    }
    .island-default .carousel-indicators-content {
        margin-left: -20px;
        margin-right: -20px;
    }

}

.island-no-margin {
    margin-top: 0;
    margin-bottom: 0;
}

.island-absolute-bottom {
    margin: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.island-absolute-top {
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.island-big-pad-top {
    padding-top: (@line-height-computed * 2); //40px
}

.island-big-pad-bottom {
    padding-bottom: (@line-height-computed * 2); //40px
}

@media (max-width: @screen-xs-max) {
    .island-absolute-top,
    .island-absolute-bottom {
        position: relative;
        top: auto;
        left: auto;
        right: auto;
    }
}

/* connected to previous, no margin between */
.island-connected {
    margin-top: 0;
    margin-bottom: 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.island + .island-connected {
    margin-top: -@line-height-computed;

    @media (max-width: @screen-xs-max) {
        margin-top: 0;
    }
}

.island-connected + .island {
    margin-top: 6px;
}

.island + .island-connected.island-condenced {
    margin-top: 0;
}

/* modifications with transparent bg */
.island-transparent {
    background-color: transparent;
}

.island-square {
    border-radius: 0;
}

.island-square-top {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.island-square-bottom {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.island-enlarged {
    margin-top: (@grid-gutter-width * 1.5); // 45px
    margin-bottom: (@grid-gutter-width * 1.5); // 45px
}

.island-condenced {
    margin-top: 0;
    margin-bottom: 0;
    padding: @grid-gutter-width;

    .island--title {
        margin: 0 -@grid-gutter-width @line-height-computed;
        padding-top: 0;
        line-height: 1.25;
    }

    &.island-header {
        margin: (-(@grid-gutter-width)) (-(@grid-gutter-width)) 0;
    }

    .island-sticky {
        margin-left: -@grid-gutter-width;
        margin-right: -@grid-gutter-width;
    }

    @media (max-width: @screen-xs-min) {
        padding: (@grid-gutter-width /2);
    }
}

.island-condenced + .island-condenced {
    margin-top: 6px;
}

.island-supercondenced {
    margin-top: 0;
    margin-bottom: 0;
    padding: (@grid-gutter-width /2);

    .island-sticky {
        margin-left: (-(@grid-gutter-width / 2));
        margin-right: (-(@grid-gutter-width / 2));
    }
}

.island-alt {
    //background-color: @brand-alt-highlight;
    background-color: fade(@paper, 60%);
}

.island .island-alt {
    background-color: fade(@paper, 20%);
}

.island-alt-bright {
    //background-color: @brand-alt-highlight-bright;
    background-color: fade(@paper, 50%);
}

.island .island-alt-bright {
    background-color: fade(@paper, 20%);
}

.island-alt-blue {
    background-color: @brand-alt;

    .island--title {
        margin-bottom: 0;
        border: none;
        color: @ink;
    }
}

.island-dark-red {
    background-color: @brand-primary;

    .island--title {
        margin-bottom: 0;
        border: none;
        color: @ink;
    }
}

.island-alt-bordered {
    //background-color: @brand-alt-highlight-bright;
    //border: 10px solid #FFF;
    background-color: fade(@paper, 50%);
    border: 15px solid fade(@ink, 10%);
}

.island-white {
    background-color: #fff;
}

