/* buttons */

/* buttons styles by shadows:
    1 - border
    2 - pressed inner shadow
    3 - gradient
    4 - focus
*/
/* btn default shadows state */
@btn-default-shadow:
    /* 1 */ inset 0 0 0 1px rgba(255 ,255 ,255 , 1),
    /* 2 */ inset 0 0 0 0 rgba(0 ,0 ,0 , 0.0),
    /* 3 */ /*inset 0 2.2em 2.2em -1.1em #fff,*/
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);
@btn-default-hover-shadow:
    /* 1 */ inset 0 0 0 1px rgba(255 ,255 ,255 , 1),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ /*inset 0 2.2em 2.2em -1.1em #fff,*/
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);
@btn-default-focus-shadow:
    /* 1 */ inset 0 0 0 1px rgba(255 ,255 ,255 , 1),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ /*inset 0 1.1em 1.1em -0.6em #fff,*/
    /* 4 */ 0 0 0 3px rgba(0, 128, 255 , 1);
@btn-default-active-shadow:
    /* 1 */ inset 0 0 0 1px rgba(255 ,255 ,255 , 1),
    /* 2 */ inset 1px 2px 3px 0 rgba(0, 0, 0, 0.2),
    /* 3 */ /*inset 0 1.1em 1.1em -0.6em #fff,*/
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);

/* btn primary shadows state */
@btn-primary-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.2),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ inset 0 2.2em 2.2em -1.1em #cb0000,
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);
@btn-primary-hover-shadow:i--expand
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.3),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ inset 0 2.2em 2.2em -1.1em #fd0000,
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);
@btn-primary-focus-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.3),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ inset 0 1.1em 1.1em -0.6em #cb0000,
    /* 4 */ 0 0 0 3px rgba(0, 128, 255, 1);
@btn-primary-active-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.3),
    /* 2 */ inset 1px 2px 3px 0 rgba(0, 0, 0, 0.2),
    /* 3 */ inset 0 1.1em 1.1em -0.6em #cb0000,
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);

/* btn success shadows state */
@btn-success-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.2),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ inset 0 2.2em 2.2em -1.1em #63ba11,
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);
@btn-success-hover-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.3),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ inset 0 2.2em 2.2em -1.1em #72db14,
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);
@btn-success-focus-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.3),
    /* 2 */ inset 0 0 0 0 rgba(0, 0, 0, 0.0),
    /* 3 */ inset 0 1.1em 1.1em -0.6em #63ba11,
    /* 4 */ 0 0 0 3px rgba(0, 128, 255, 1);
@btn-success-active-shadow:
    /* 1 */ inset 0 0 0 1px rgba(0, 0, 0, 0.3),
    /* 2 */ inset 1px 2px 3px 0 rgba(0, 0, 0, 0.2),
    /* 3 */ inset 0 1.1em 1.1em -0.6em #63ba11,
    /* 4 */ 0 0 0 0 rgba(0, 128, 255, 1);

.btn {
    padding-top: 13px;
    padding-bottom: 12px;
    font-weight: bold;
    vertical-align: top;

    &.btn-block {
        margin-bottom: 1em;
    }

    &.btn-support {
        height: auto;
    }

    .i {
        margin: -8px 5px -5px 0;
    }
}

.btn-default {
    .box-shadow(@btn-default-shadow);
    background: #e0e0ed;
    border: none;
    color: @brand-primary-btn-dark;
    text-shadow: 0 1px 0 #fff;
    text-transform: uppercase;
    .transition(all 0.2s);

    &:hover {
        .box-shadow(@btn-default-hover-shadow);
        background-color: #ECECFB;
        color: @brand-primary-btn;
        text-decoration: none;
    }

    &:focus {
        .box-shadow(@btn-default-focus-shadow);
        outline:none;
    }

    &:active {
        .box-shadow(@btn-default-active-shadow);
        outline:none;
        .transition-duration(0.1s);
    }

    &.disabled,
    &[disabled] {
        opacity: 0.5;
        cursor: default;
        .box-shadow(none);

        &:hover,
        &:focus,
        &:active {
            background: #e0e0ed;
        }
    }

    .i {
        fill: @brand-primary-btn-dark;
    }

    &.btn-svg {
        color: @gray-alt;

        .i {
            width: 20px;
            height: 20px;
            fill: @gray-alt;
        }
    }
}

.btn-primary {
    .box-shadow(@btn-primary-shadow);
    background: #a10000;
    border: none;
    color: #fff;
    text-shadow: 0 1px 0 #950000;
    text-transform: uppercase;
    .transition(all 0.2s);

    &:hover {
        background-color: #cd0000;
        text-shadow: 0 1px 0 #be0000;
        text-decoration: none;
        .box-shadow(@btn-primary-hover-shadow);
    }

    &:focus {
        background-color: #a10000;
        border: none;
        .box-shadow(@btn-primary-focus-shadow);
        text-shadow: none;
        outline:none;
    }

    &:active {
        .box-shadow(@btn-primary-active-shadow);
        outline:none;
        .transition-duration(0.1s);
    }

    &.disabled,
    &[disabled] {
        opacity: 0.5;
        cursor: default;
        .box-shadow(none);

        &:hover,
        &:focus,
        &:active {
            background: #b50000;
        }
    }
}

.btn-success {
    .box-shadow(@btn-success-shadow);
    background: #4d871d;
    border: none;
    color: #fff;
    text-shadow: 0 1px 0 #4a8714;
    text-transform: uppercase;
    .transition(all 0.2s);

    &:hover {
        background-color: #529c0e;
        text-shadow: 0 1px 0 #529c0e;
        text-decoration: none;
        .box-shadow(@btn-success-hover-shadow);
    }

    &:focus {
        background-color: #4d871d;
        border: none;
        outline:none;
        .box-shadow(@btn-success-focus-shadow);
    }

    &:active {
        .box-shadow(@btn-success-active-shadow);
        outline:none;
        text-shadow: none;
        .transition-duration(0.1s);
    }

    &.disabled,
    &[disabled] {
        opacity: 0.5;
        cursor: default;
        .box-shadow(none);

        &:hover,
        &:focus,
        &:active {
            background: #4d871d;
        }
    }
}

.btn-text-left {
    text-align: left;
}

.btn-text-lg {
    font-size: @font-size-large;
}

.btn-lg {
    .button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @border-radius-large);
    min-width: inherit;
    height: auto;
}
.btn-sm {
    .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @border-radius-small);
    min-width: inherit;
    height: auto;
}
.btn-xs {
    .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-xs-small; @line-height-small; @border-radius-small);
    min-width: inherit;
    height: auto;
}

.btn-link {
    padding: 0;
    margin: 0;
    border: 0;
}

/* mobile store buttons */
.btn-appstore,
.btn-gplay {
    display: inline-block;
    overflow: hidden;
    margin: 0 5px 5px 0;
    width: 142px;
    min-width: inherit;
    height: 40px;
    color: transparent;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    text-indent: 150%;
}

.btn-email {
    display: inline-block;
    position: relative;
    top: -2px;
    margin-left: 10px;
    padding-left: 29px;
    width: 65px;
    height: 20px;
    font-size: 11px;
    font-weight: bold;
    line-height: 20px;
    color: @ink;
    background: url("../icons/svg/email.svg");
    background-color: #aaa;
    background-size: 20px auto;
    background-repeat: no-repeat;
    background-position: 8% 50%;
    border-radius: 2px;
}

.no-svg .btn-appstore,
.no-svg .btn-appstore:active {
    background: url("../img/btn-appstore.png");
}

.no-svg .btn-gplay,
.no-svg .btn-gplay:active {
    background: url("../img/btn-gplay.png");
}

.no-svg .btn-appstore-es,
.no-svg .btn-appstore-es:active {
    background: url("../img/btn-appstore-es.png");
}

.no-svg .btn-gplay-es,
.no-svg .btn-gplay-es:active {
    background: url("../img/btn-gplay-es.png");
}

.svg .btn-appstore,
.svg .btn-appstore:active {
    background: url("../img/btn-appstore.svg");
}

.svg .btn-gplay,
.svg .btn-gplay:active {
    background: url("../img/btn-gplay.svg");
}

.svg .btn-appstore-es,
.svg .btn-appstore-es:active {
    background: url("../img/btn-appstore-es.svg");
}

.svg .btn-gplay-es,
.svg .btn-gplay-es:active {
    background: url("../img/btn-gplay-es.svg");
}

/* hide buttons depending of mobile os */
.android .btn-appstore {
    display: none;
}

.ios .btn-gplay {
    display: none;
}

