/* btn group */
.btn-group {
    .clearfix;

    .btn {
        width: auto;
        min-width: inherit;
        font-size: @font-size-larger;
        line-height: 21px;

        &.btn-default {
            &.active {
                background: #a10000;
                border: none;
                .box-shadow(@btn-primary-active-shadow);
                color: #fff;
                text-shadow: 0 1px 0 #950000;
                outline:none;
            }
        }
    }

    &.btn-group-block {
        display: block;
        float: none;
        width: 100%;
        margin: 0 0 15px;
    }

    &.btn-group-count-3 {
        .btn {
            width: 33.3%;
        }
    }
}

/* footer support buttons */
.btn-support {
    display: block;
    padding: 10px 25px;
    text-align: center;
    background: @brand-primary-bright;
    border-radius: @border-radius-base;
    color: #FFF;

    &:hover,
    &:focus,
    &:active {
        text-decoration: none;
        color: @brand-primary-bright;
        background: #FFF;

        .i {
            fill: @brand-primary-bright;
        }
    }
}

.btn-support--small {
    display: block;
    font-size: 13px;
}

.btn-support--key {
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
}

/* footer support buttons end */


/* social share buttons */
.btn-share {
    display: inline-block;
    margin: 3px;

    .i {
        width: 57px;
        height: 57px;
    }
}
/* social share buttons end */

/* collapse trigger */
.collapse-trigger {
    &.collapse-trigger-arrows {
        background: transparent;
        border: 0;
        padding: 0 20px;
        color: @link-color;
        font-size: @font-size-larger;
        font-weight: bold;
        outline: none;

        &:hover {
            color: @link-hover-color;
        }

        .i {
            display: inline-block;
            height: 21px;
            width: 21px;
            float: none;
            margin-left: 5px;

            &.i--collapse {display: inline-block;}
            &.i--expand {display: none;}
        }
        
        .collapse-trigger-arrows--lable-show {
            display: none;
        }

        .collapse-trigger-arrows--lable-hide {
            display: inline;
        }

        &.collapsed {
            .i--collapse {display: none;}
            .i--expand {display: inline-block;}

            .collapse-trigger-arrows--lable-show {
                display: inline;
            }

            .collapse-trigger-arrows--lable-hide {
                display: none;
            }
        }
    }

    &.collapse-trigger-cross {
        background: transparent;
        border: 0;
        padding: 0 20px;
        color: @link-color;
        font-size: @font-size-larger;
        font-weight: bold;
        outline: none;

        &:hover {
            color: @link-hover-color;
        }

        .i {
            height: 21px;
            width: 21px;
            vertical-align: text-bottom;
        }
    }
}
/* collapse trigger end*/

.btn-block-xs {
    @media (max-width: @screen-xs ) {
        width: 100%;
        min-width: inherit;
        max-width: 100%;
        display: block;
        margin-bottom: (@grid-gutter-width / 2);
    }
}
